* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.card-item {
  display: flex;
  align-items: center;
  margin-bottom: 5px;
}

.card-item-img {
  max-width: 25px;
  margin-right: 5px;
}

.card-item-text {
  margin: 0;
}

.card-header {
  display: flex;
  align-items: center;
  line-height: 1;
  margin-bottom: 24px;
}

.card-header-img {
  max-width: 35px;
}

.card-header-text {
  margin: 0;
  margin-left: 10px;
  font-weight: 600;
  font-size: 35px;
}
.card-body-text-style {
  font-weight: 700;
}
